
















































































































































































.video-box {
  width: 100%;
  height: 420px;
  position: relative;
  .icon-zanting {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: #f5f5f5;
    font-size: 100px;
  }
}

#my-video {
  width: 100%;
  height: 420px;
}

.my-scroll {
  height: calc(100vh - 420px);
  width: 100%;
}

.movie-intro-desc {
  position: relative;
  padding: 30px;
  border-bottom: 20px solid #f5f5f5;
  color: #666;
  font-size: 26px;
}

.name-box {
  display: flex;
  align-items: center;
  color: #333;
  font-size: 38px;
}

.tag-box {
  border: 0.02rem solid #b3b5b9;
  border-radius: 4px;
  box-sizing: border-box;
  font-size: 20px;
  margin-left: 4px;
}

.tag-d {
  color: #fff;
  padding: 0 4px;
  background-color: #b3b5b9;
}

.tag-imax {
  color: #b3b5b9;
  padding: 0 4px;
}

.yellow {
  color: #ffc600;
  font-size: 32px;
}

.sc-box {
  margin: 10px 0;
}

.buy-tickets {
  position: absolute;
  top: 50%;
  right: 30px;
  transform: translateY(-50%);
}

.play-title {
  padding: 30px;
  border: 1px solid #f5f5f5;
  font-size: 30px;
  color: #333;
}

.video-item {
  display: flex;
  padding: 30px;
  padding-left: 0;
  margin-left: 30px;
  border-bottom: 1px solid #f5f5f5;
}

.img-box {
  position: relative;
  width: 220px;
  height: 124px;
}

.img-box img {
  width: 220px;
  height: 124px;
}

.txt {
  position: absolute;
  left: 10px;
  bottom: 10px;
  color: #fff;
  font-size: 24px;
}

.video-name {
  /* 多行文字溢出打省略号 */
  flex: 1;
  margin-left: 30px;
  word-break: break-all;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
  font-size: 30px;
  color: #666;
}

.red {
  color: #f03d37;
}
